import React from 'react';
import { formatSize, getIcon } from '../../../fileTypeHelper';
import {
  PreviewHeaderWrapper,
  CardIconWrapper,
  CardContentWrapper,
  CardContentInnerWrapper,
  ContentHeader,
  ContentDesc,
} from './styled';

interface Props {
  name: string | undefined;
  size: number;
  type: string;
  mode?: string;
  isMobile?: boolean;
  renderExtra?: () => React.ReactElement;
}

function EmbedPreviewHeader(props: Props) {
  const { name, size, type, mode, isMobile, renderExtra } = props;

  return (
    <PreviewHeaderWrapper mode={mode}>
      <CardIconWrapper>
        <img
          src={getIcon(type)}
          alt="icon"
          style={isMobile ? { width: 23, height: 24 } : { width: 16, height: 23 }}
        />
      </CardIconWrapper>
      <CardContentWrapper>
        <CardContentInnerWrapper>
          <ContentHeader mode={mode}>{name}</ContentHeader>
          <ContentDesc mode={mode}>{formatSize(size)}</ContentDesc>
        </CardContentInnerWrapper>
      </CardContentWrapper>
      {renderExtra && renderExtra()}
    </PreviewHeaderWrapper>
  );
}

export default EmbedPreviewHeader;
